<template>
  <div class="main-div">
    <div class="main-bg">
      <DialogueTheme class="theme" :msgs="firstMsgs" :speaker="speaker" :finishConversation="finishFirstConversation" v-if="showFirstDialogue" />
      <DialogueTheme class="theme" :msgs="msgs_5_1" :speaker="speaker" :finishConversation="finish5_1Conversation" v-if="show5_1Dialogue" />
      <DialogueTheme class="theme" :msgs="msgs_5_2" :speaker="speaker" :finishConversation="finish5_2Conversation" v-if="show5_2Dialogue" />
      <Selection id="selection" :items="items" v-show="showSelection" :finishSelection="finishSelection" />
      <div id='avator'></div>
    </div>
  </div>
</template>

<script>
import DialogueTheme from '@/components/DialogueTheme'
import Selection from '@/components/Selection'

export default {
  name: 'CommuteSouthMainStation',
  components: {
    DialogueTheme,
    Selection
  },
  data () {
    return {
      speaker: '? ? ?',
      showFirstDialogue: true,
      firstMsgs: ['歡迎搭乘１００１怪奇客運，本班車從「正常」往「怪奇」，沿途各站皆無停靠。您是否要搭乘？'],
      items: ['沒，我沒要搭乘。', '對，我要上車。'],
      showSelection: false,
      show5_1Dialogue: false,
      msgs_5_1: ['這樣呀，反正還有很多班，你要搭再來吧。'],
      show5_2Dialogue: false,
      msgs_5_2: ['呵呵⋯客人是在怪奇博物館上班吧？你去上班需要通勤嗎？通勤啊～通勤，還有比這更浪費時間、浪費生命的事嗎？', '你不明白的對吧？那麼你就在這轉乘陣裡好好體會吧。如果你無法破解轉乘陣，你可能會把所有的人生都浪費在這裡唷，就跟那些上班族一樣，呵呵呵⋯⋯']
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', true)
  },
  methods: {
    finishFirstConversation (msgs) {
      this.showFirstDialogue = false
      this.showSelection = true
    },
    finishSelection (index) {
      this.showSelection = false
      console.log(index)
      if (index === 0) {
        this.show5_1Dialogue = true
      } else if (index === 1) {
        this.show5_2Dialogue = true
      }
    },
    finish5_1Conversation (msgs) {
      // this.$router.go()  //safari bug
      // this.$router.go(this.$router.currentRoute)  safari bug
      window.location.reload()
    },
    finish5_2Conversation (msgs) {
      this.$router.push({name: 'CommuteTransferDiagramOne'})
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/south-main-station-bg.png');
      background-size: contain;
      position: relative;
      #avator {
        @extend fullScreenImage;
        background-image: url('~@/assets/images/commute/passenger_shadow.png');
        position: absolute;
        z-index: 0;
      }
      .theme {
        bottom: 15%;
      }
    }
  }
</style>



